import { WeaponDamage, WeaponHandedness, resolveWeaponHandedness, AttackType } from "./weapon";
import BaseModel, { Model, Map, PropertyCollection } from ".";

@Model
export default class Projectile extends BaseModel {
    id: string;
    attackType: AttackType;
    handedness: WeaponHandedness;

    @Map([null, 'Name'])
    name: string;

    @Map([null, 'Type'])
    type: string;

    @Map([null, 'Is Melee Weapon'], o => o.toLowerCase() == 'yes')
    isMeleeWeapon: boolean;
    
    @Map([null, 'Can Flinch'], o => o.toLowerCase() == 'yes')
    canFlinch: boolean;

    @Map([null, 'Point Cost'], parseInt)
    pointCost: number;
    
    @Map([null, 'Value'], parseFloat)
    value: number;
    
    @Map(['Damage [No Armor]', {head: 'Head', torso: 'Torso', legs: 'Legs'}], PropertyCollection(parseFloat))
    damageNoArmor: WeaponDamage;

    @Map(['Damage [Light Armor]', {head: 'Head', torso: 'Torso', legs: 'Legs'}], PropertyCollection(parseFloat))
    damageLightArmor: WeaponDamage;
    
    @Map(['Damage [Medium Armor]', {head: 'Head', torso: 'Torso', legs: 'Legs'}], PropertyCollection(parseFloat))
    damageMediumArmor: WeaponDamage;

    @Map(['Damage [Heavy Armor]', {head: 'Head', torso: 'Torso', legs: 'Legs'}], PropertyCollection(parseFloat))
    damageHeavyArmor: WeaponDamage;


    @Map([null, {averageDamage: 'Avg. Damage', dps: 'DPS'}], PropertyCollection(parseFloat))
    computedDamage: {
        averageDamage: number;
        dps: number;
    }

    @Map(['Speed', {draw: 'Draw', combo: 'Reload'}], PropertyCollection(parseFloat))
    speed: {
        draw: number;
        reload: number;
    }

    @Map(['Speed', {computedSpeed: 'Attack Speed'}], PropertyCollection(parseFloat))
    computedSpeed: {
        attack: number;
    }

    @Map([null, 'Max Ammo'], parseFloat)
    maxAmmo: number;
    
    @Map([null, 'Projectile Speed'], parseFloat)
    projectileSpeed: number;

    @Map([null, 'Gravity Scale'], parseFloat)
    gravityScale: number;

    @Map(['Miscellaneous', 'Wood Damage'], parseFloat)
    woodDamage: number;

    @Map(['Miscellaneous', 'Stone Damage'], parseFloat)
    stoneDamage: number;

    @Map(['Miscellaneous', 'Peasant Perk'], o => o != null && o.toLowerCase() == 'yes')
    isPeasantWeapon: boolean;

    getSortKey() {
        return this.name + 'projectile' + (this.canFlinch ? '1' : '0');
    }

    constructor(data?: string[]) {
        super(data);
        
        if (data == null) {
            return;
        }
        
        this.attackType = AttackType.Projectile;
        this.handedness = resolveWeaponHandedness(this.type);    

        this.generateId();

        return;
    }

    private generateId() {
        let handednessStr = '';
        if (this.handedness & WeaponHandedness.One && this.handedness & WeaponHandedness.Two) {
            handednessStr = '3';
        } else if(this.handedness & WeaponHandedness.One) {
            handednessStr = '1';
        } else if (this.handedness & WeaponHandedness.Two) {
            handednessStr = '2';
        }

        this.id = `${this.name.replace(/ /g, '-')}-projectile-${this.canFlinch ? '1' : '0'}${handednessStr}`;
    }
}