

























































































































































































































































































import Vue from 'vue';
import { Component, Prop, Watch } from 'vue-property-decorator';
import WeaponLabel from './partial/weapon-label.vue';
import WeaponInventoryCost from './partial/weapon-inventory-cost.vue';
import AttributeBars from './partial/attribute-bars.vue';
import DataProvider, {Spreadsheet} from '../ts/data';
import {Weapon, WeaponHandedness, AttackType} from '../ts/model/weapon';
import {DisplayWeapon} from '../ts/view-model/weapon';
import Projectile from '../ts/model/projectile';

@Component({
    components: {WeaponLabel, AttributeBars, WeaponInventoryCost}
})
export default class App extends Vue {
    $refs: Vue['$refs'] & {
        searchInput: HTMLInputElement;
    }

    @Prop()
    googleApiKey: string;

    isLoading = true;
    showCopyright = false;

    weapons: Weapon[] = [];
    uniqueWeaponsByNameCount = 0;
    displayWeapons: DisplayWeapon[] = [];

    projectiles: Projectile[] = [];

    filteredWeapons: DisplayWeapon[] = [];

    defaults = {
        search: '',
        showOneHandedWeapons: true,
        showTwoHandedWeapons: true,
        showAltModeWeapons: false,
        showStrikeWeapons: true,
        showThrustWeapons: false,
        showProjectiles: true,
        sortValues: false,
        sortDamage: false,
        sortAttackSpeed: false,
        sortReach: false,
        globalCompare: true,
        attributesLayout: 'grid',
        attributesCollapsed: false,
        weaponsListCollapsed: false,
    };

    search: string = null;
    showOneHandedWeapons: boolean = null;
    showTwoHandedWeapons: boolean = null;
    showAltModeWeapons: boolean = null;
    showStrikeWeapons: boolean = null;
    showThrustWeapons: boolean = null;
    showProjectiles: boolean = null;
    sortValues: boolean = null;
    sortDamage: boolean = null;
    sortAttackSpeed: boolean = null;
    sortReach: boolean = null;
    globalCompare: boolean = null;
    attributesLayout: string = null;
    attributesCollapsed: boolean = null;
    weaponsListCollapsed: boolean = null;

    highestWeaponComputedAverageDamage = 0;
    highestWeaponComputedAttackSpeed = 0;
    highestWeaponLength = 0;

    dataProvider: DataProvider = null;

    get selectedWeapons() {
        const result: DisplayWeapon[] = [];
        for (let i = 0; i < this.displayWeapons.length; i++) {
            if (this.displayWeapons[i].isSelected) {
                result.push(this.displayWeapons[i]);
            }
        }
        
        return result;
    }

    async loadSpreadsheetData() {
        this.weapons = await this.dataProvider.getWeapons();
        this.projectiles = await this.dataProvider.getProjectiles();

        const items = [...this.weapons, ...this.projectiles].map(o => new DisplayWeapon(o));

        this.uniqueWeaponsByNameCount = new Set(items.map(o => o.name)).size;
        this.displayWeapons = items.map(o => new DisplayWeapon(o));
    }

    async mounted() {
        this.resetFilters();

        this.dataProvider = new DataProvider(this.googleApiKey);
        await this.dataProvider.init();

        await this.loadSpreadsheetData();       
        this.isLoading = false;     
        this.showCopyright = true;
        this.deserializeState();
        this.applyWeaponFilters();
    }

    searchKeyUp() {
        console.log('keyup!')
        if (this.search != this.$refs.searchInput.value) {
            this.search = this.$refs.searchInput.value;
        }
    }

    @Watch('sortValues')
    onSortValuesChanged() {
        this.sortDamage = this.sortValues;
        this.sortAttackSpeed = this.sortValues;
        this.sortReach = this.sortValues;
    }

    @Watch('search')
    @Watch('showOneHandedWeapons')
    @Watch('showTwoHandedWeapons')
    @Watch('displayWeapons')
    @Watch('showAltModeWeapons')
    @Watch('showStrikeWeapons')
    @Watch('showThrustWeapons')
    @Watch('showProjectiles')
    applyWeaponFilters() {
        if (this.isLoading) {
            return;
        }

        this.serializeState();

        const search = this.search.toLowerCase();
        const result: DisplayWeapon[] = [];

        for (let i = 0; i < this.displayWeapons.length; i++) {
            const weapon = this.displayWeapons[i];

            if (
                (search.length == 0 || weapon.name.toLowerCase().indexOf(this.search.toLowerCase()) != -1) &&

                (
                    (this.showOneHandedWeapons && (weapon.handedness & WeaponHandedness.One)) ||
                    (this.showTwoHandedWeapons && (weapon.handedness & WeaponHandedness.Two))
                ) &&

                (this.showAltModeWeapons || !weapon.altMode) &&

                (
                    (this.showStrikeWeapons && weapon.attackType == AttackType.Strike) ||
                    (this.showThrustWeapons && weapon.attackType == AttackType.Thrust) ||
                    (this.showProjectiles && weapon.attackType == AttackType.Projectile)
                )
             ) {
                result.push(weapon);
            }
        }

        this.filteredWeapons = result;
    }

    @Watch('selectedWeapons')
    onSelectedWeaponsChanged() {
        if (this.isLoading) {
            return;
        }
        
        this.serializeState();
    }

    // All the properties that are saved in state
    @Watch('search')
    @Watch('showOneHandedWeapons')
    @Watch('showTwoHandedWeapons')
    @Watch('displayWeapons')
    @Watch('showAltModeWeapons')
    @Watch('showStrikeWeapons')
    @Watch('showThrustWeapons')
    @Watch('showProjectiles')
    @Watch('sortValues')
    @Watch('globalCompare')
    @Watch('attributesLayout')
    stateChanged() {
        if (this.isLoading) {
            return;
        }

        this.serializeState();
    }

    serializeState() {
        this.$router.replace({
            query: {
                ...this.$route.query,
                search: this.search,
                oneHanded: JSON.stringify(this.showOneHandedWeapons),
                twoHanded: JSON.stringify(this.showTwoHandedWeapons),
                altMode: JSON.stringify(this.showAltModeWeapons),
                strike: JSON.stringify(this.showStrikeWeapons),
                thrust: JSON.stringify(this.showThrustWeapons),
                sortValues: JSON.stringify(this.sortValues),
                layout: this.attributesLayout,
                selected: this.selectedWeapons.map(o => o.id).join(',')
            }
        })
    }

    deserializeState() {
        this.search = this.$route.query.search as string || '';
        this.showOneHandedWeapons = JSON.parse(this.$route.query.oneHanded as string || JSON.stringify(this.defaults.showOneHandedWeapons)) as boolean;
        this.showTwoHandedWeapons = JSON.parse(this.$route.query.twoHanded as string || JSON.stringify(this.defaults.showTwoHandedWeapons)) as boolean;
        this.showAltModeWeapons = JSON.parse(this.$route.query.altMode as string || JSON.stringify(this.defaults.showAltModeWeapons)) as boolean;
        this.showStrikeWeapons = JSON.parse(this.$route.query.strike as string || JSON.stringify(this.defaults.showStrikeWeapons)) as boolean;
        this.showThrustWeapons = JSON.parse(this.$route.query.thrust as string || JSON.stringify(this.defaults.showThrustWeapons)) as boolean;
        this.sortValues = JSON.parse(this.$route.query.sortValues as string || JSON.stringify(this.defaults.sortValues)) as boolean;
        this.attributesLayout = this.$route.query.layout as string || this.defaults.attributesLayout;
        const selectedIds = (this.$route.query.selected as string || '').split(',');

        for (let weapon of this.displayWeapons) {
            weapon.isSelected = selectedIds.indexOf(weapon.id) !== -1;
        }
    }

    selectAll() {
        for (let weapon of this.displayWeapons) {
            weapon.isSelected = true;
        }
    }

    unSelectAll() {
        for (let weapon of this.displayWeapons) {
            weapon.isSelected = false;
        }

        this.weaponsListCollapsed = false;
    }

    resetFilters() {
        for (let key in this.defaults) {
            this.$set(this, key, this.defaults[key]);
        }
    }

    reload() {
        
    }

    get canSelectAll() {
        for (let i = 0; i < this.displayWeapons.length; i++) {
            if (!this.displayWeapons[i].isSelected) {
                return true;
            }
        }

        return false;
    }

    get canUnselectAll() {
        for (let i = 0; i < this.displayWeapons.length; i++) {
            if (this.displayWeapons[i].isSelected) {
                return true;
            }
        }

        return false;
    }
}
